@import "https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap";
@import "https://fonts.googleapis.com/css2?family=Fredoka:wght@300;400&display=swap";
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: 'Fredoka',sans-serif;
}
:root{
	--button-bg-dark:#1A1818;
	--main-bg-color:#e0e0e0;
}
body {
	background-color: #e0e0e0;
	margin: 0;
	font-family: 'Open Sans', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
a{
	text-decoration: none;
}
button {
	transition: all 0.7s;

	&:enabled {
		cursor: pointer;
		border: none;
	}
	&:disabled {
		outline: none;
		box-shadow: none;
	}
	
}
::-webkit-scrollbar {
	width: 12px;
}
::-webkit-scrollbar-track {
	background: #1f2937;
}
::-webkit-scrollbar-thumb {
	background-color: beige;
}
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
