.itemListContainer {
  display: flex;
  flex-direction: column;
  width: 70%;
  height: fit-content;
  margin: auto;
  row-gap: 10%;
  justify-items: center;
  background-color: #e0e0e0;
  align-items: center;
  margin-top: 5vh;
  h1 {
    text-align: center;
  }
  .itemList {
    padding: 5%;
    display: flex;
    width: 95%;
    justify-content: space-between;
    align-items: flex-end;
    background-color: #e0e0e0;
    flex-wrap: wrap; /*IMPORTANTE*/
    height: auto;
    flex-flow: wrap;
    row-gap: 50px;
    .item {
      display: flex;
      flex-direction: column;
      cursor: default;
      border-radius: 20px 20px 0 0;
      background-color: white;
      box-shadow: 20px 20px 31px #d0d0d0, -20px -20px 31px #f0f0f0;
      min-height: 320px;
      height: 20vh;
      width: 200px;
      transition: all 0.5s ease;
      border-radius: 0px 0 10px 10px;

      h1 {
          
        font-size: 1em;
        font-weight: 300;
        height: 30%; 

      }
      a {
        color: white;
        text-decoration: none;
        height: 15%;
        button {
          height: 100%;
          width: 100%;
          background-color: #1a1818;
          color: white;
          transition: all 0.7s;
          &:hover {
            background: lighten(#1a1818, 10%);
          }
         
        }
      }
    
      .infoItem {
        display: flex;
        padding: 10%;
        flex-direction: column;
        height: 100%;
        justify-content: space-between;
        
        background-color: white;
        img{
            width: 80%;
        }
        span{
            width: 50%;
            height: 1pt;
            background-color: #1a181835;
        }
      }
      &:hover {
        transform: scale(1.05);
      }
    }
  }
}
@media screen and(max-width:768px) {
  
 
}
@media screen and(max-width:550px) {
.itemListContainer{
  h1{
    height: 5vh;
  }
  
  .itemList{
    justify-content: center;
    .item{
      width: 100%;
      height:50vh;
      max-width: 450px;
    }
  }
}
}